<template>
    <div>
        <loading :active.sync="isLoading"></loading>
        <div class="container ec-container mt-3">
            <div class="row justify-content-center">
                <div class="col-md-10">
                    <nav aria-label="breadcrumb">
                        <ol class="breadcrumb bg-transparent pl-0">
                            <li class="breadcrumb-item h5"><router-link to="/" class="text-brown">Home</router-link ></li>
                            <li class="breadcrumb-item h5"><router-link to="/products" class="text-brown"></router-link></li>
                            <li class="breadcrumb-item active h5" aria-current="page" >{{ product.title }}</li>
                        </ol>
                    </nav>
                    <div class="row mb-5">
                        <div class="col-md-6">
                            <div >
                                <img :src="product.imageUrl"  alt="" style="
                                    height: 400px;
                                     width:100%;                                     
                                    background-size: cover;
                                    background-position: center center;
                                " class="img-fluid">                                
                            </div>
                        </div>
                        <div class="col-md-6">
                             <h2 class="font-weight-bold text-black">
                                {{ product.title }}
                            </h2>
                            <p>{{ product.content }}</p>
                            <div>
                               
                                <p class="mt-5">
                                <i class="fas fa-truck"></i> <br />
                                </p>
                                <div>
                                <ul>
                                    <li>2-3<br />
                                        </li>
                                    <li>1-2<br />
                                        </li>
                                    <li> <br />
                                        </li>
                                </ul>
                                </div>                                
                            </div>
                                <div class="d-flex justify-content-between align-items-baseline">
                                <div class="h4" v-if="!product.price">{{ product.origin_price }} </div>
                                <del class="h6" v-if="product.price"> {{ product.origin_price }} </del>
                                <div class="h4 text-danger" v-if="product.price">TWD {{ product.price }} </div>
                            </div>

                            <select name="" class="form-control mt-3" v-model="product.num">
                                <option :value="num" v-for="num in 10" :key="num">
                                     {{num}} {{product.unit}}
                                </option>
                            </select>
                            <div class="input-group mt-3">
                                <div class="text-muted text-nowrap mr-3 mr-auto h3 mt-3">
                                 <strong>{{ product.num * product.price | currency }}</strong> 
                                </div>
                                <button type="button" class="btn btn-brown "
                                @click="addCart(product.id, product.num)">
                                <i class="fas fa-spinner fa-spin " v-if="product.id === status.loadingItem"></i>
                                
                                </button>
                            </div>
                        </div>
                        <div class="my-4 bg-light p-3" role="alert">
                            <h2 class="text-center h2"></h2>
                            <p>(3~ 5cm)/180

                                 <br>

                                <br>
                                
                                <br>
                                <br>
                                
                                <br>
                                
                                <br>
                                
                            </p>
                        </div>
                    </div>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
import AlertMsg from '@/plugin/MsgAlert';
export default {
    data() {
        return {
            status: {
                loadingItem: '',
            },
            product: {
                num: 1,
                imageUrl: [],
            },
            isLoading: false,
        }
    },methods: {
        getProduct() {
            const vm = this;
            const { id } = this.$route.params;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
             vm.isLoading = true;
            this.$http.get(api).then((response) =>{
                // console.log(response)
                response.data.product.num = 1;
                vm.product = response.data.product;
                // vm.product.num =1;
                // console.log(response.data.product)
                vm.status.loadingItem = '';
                vm.isLoading = false;                
            })                
        },
        addCart(id ,qty=1){
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
            vm.status.loadingItem = id;
            const cart = {
                product_id : id,
                qty,
            };
            this.$http.post(api,{data :cart}).then((response) =>{
                 AlertMsg.fire({
                    title: '',
                    icon: 'success',
                });
                vm.$bus.$emit("upDataCart");
                // console.log(response.data)
                vm.status.loadingItem = ''; 
                // vm.getCart();
                //  $('#productModal').modal('hide');               
            })
        },
    },
    created() {
        this.getProduct();
    },
}
</script>


