<template>
    <div>
        <div class="container main-contant py-5">
            <div class="form-row text-center">
               <div class="col-12 col-md"><!-- 1 -->
                    <div class="alert alert-success alert-rounded" role="alert">
                        1.
                    </div>
               </div>
               <div class="col-12 col-md">
                    <div class="alert alert-warning alert-rounded " role="alert">
                        2.
                    </div>
                </div>
                <div class="col-12 col-md">
                    <div class="alert alert-dark alert-rounded" role="alert">
                        3.
                    </div>
                </div>                
           </div>
            <div class="row justify-content-center mt-5">
                <div class="col-md-8">
                    <h2 class="mt-2 text-brown font-weight-bold"></h2>
                    <validation-observer class="col-md-6" v-slot="{ invalid }">
                    <form  @submit.prevent="createOrder">
                        
                        <validation-provider  rules="required|email" v-slot="{ errors,classes }">
                            <div class="form-group">
                                 <!--  -->
                                <label for="email">Email</label>
                                <input id="email" type="email" name="Email" v-model="form.user.email"
                                    class="form-control" :class="classes" placeholder=" Email">
                                <!--  -->
                                <span class="invalid-feedback">{{ errors[0] }}</span>
                            </div>                           
                        </validation-provider>  
                                              
                        <validation-provider  rules="required" v-slot="{ errors,classes }">
                            <div class="form-group">
                                 <!--  -->
                                <label for="username"></label>
                                <input id="username" type="text" name="" v-model="form.user.name"
                                    class="form-control" :class="classes" placeholder="">
                                <!--  -->
                                <span class="invalid-feedback">{{ errors[0] }}</span>
                            </div>                           
                        </validation-provider>
                        
                        <validation-provider  rules="required|integer" v-slot="{ errors,classes }">
                            <div class="form-group">
                                 <!--  -->
                                <label for="usertel"></label>
                                <input id="usertel" type="tel" name="" v-model="form.user.tel"
                                    class="form-control" :class="classes" placeholder="">
                                <!--  -->
                                <span class="invalid-feedback">{{ errors[0] }}</span>
                            </div>                           
                        </validation-provider>                       

                        <validation-provider  rules="required" v-slot="{ errors,classes }">
                            <div class="form-group">
                                 <!--  -->
                                <label for="useraddress"></label>
                                <input id="useraddress" type="tel" name="" v-model="form.user.address"
                                    class="form-control" :class="classes" placeholder="">
                                <!--  -->
                                <span class="invalid-feedback">{{ errors[0] }}</span>
                            </div>                           
                        </validation-provider>
                         <div class="form-group">
                        <label for="comment"></label>
                        <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
                        </div>

                        
                        <div class="text-right mt-4" >
                        <button class="btn btn-danger" :disabled="invalid"></button>
                        </div> 
                    </form>
                </validation-observer>
                </div>
            </div>


        </div>
    </div>
</template>
<style>
.alert-rounded{/**/
    border-radius: 50px;
}
</style>

<script>
export default {
    data() {
        return {
            
            isLoading:false,            
            form:{
                user:{
                    name:'',
                    email:'',
                    tel:'',
                    address:'',                    
                },
                message:'',
            }
        }
    },
    methods: {
        createOrder(){
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
            const order = vm.form;
            vm.isLoading = true;            
            this.$http.post(api,{data:order}).then((response) =>{
                console.log('',response.data.orderId)  
                if(response.data.success){ 
                    vm.$bus.$emit("upDataCart");
                    vm.$router.push(`/ordercheckout/${response.data.orderId}`) 
                                      
                    // vm.$router.push(`/ordercheckout/${response.data.orderId}`)
                    console.log('123' )
                }else{
                    console.log(response.data.message )
                }
                
                vm.isLoading = false;                
                
            })
        }
    },
}
</script>


